<template>
  <div class="Q2">
    <h1>Q2</h1>
    <p style="margin-button: 10px">ex:(c000001)</p>
    <input v-model="userQuery" />
    <p>{{ userQuery }}  : {{ total }}</p>
    <button @click="qs2()" style="margin-top: 10px"></button>
  </div>
</template>

<script>
import { db } from "../db";
import { collection, query, where, getDocs } from "firebase/firestore";

export default {
  data() {
    return {
      userQuery: "",
      total: 0,
    };
  },
  methods: {
    async qs2() {
      this.total = 0;
      const Ref_Order = collection(db, "Order");
      const q2_1 = query(Ref_Order, where("customerId", "==", this.userQuery));
      const querySnapshot_1 = await getDocs(q2_1);
      querySnapshot_1.forEach((doc) => {
        this.total += doc.data().price;
      });
    },
  },
};
</script>
