<template>
  <div class="Q3">
    <h1>Q3</h1>
    <p style="margin-button: 10px">
      ex:(c000001)
    </p>
    <input v-model="userQuery" />
    <p>{{ userQuery }}  : {{ result }}</p>
    <p> {{ buycount }} </p>
    <button @click="qs3()" style="margin-top: 10px"></button>
  </div>
</template>

<script>
import { db } from "../db";
import { collection, query, where, getDocs } from "firebase/firestore";

export default {
  data() {
    return {
      userQuery: "",
      temp: new Map(),
      result: "",
      buycount: 0,
    };
  },
  methods: {
    async qs3() {
      // console.log(typeof(this.temp));
      this.temp = new Map();
      this.result = "";
      this.buycount = 0;
      // console.log(typeof(this.temp));
      var count = 1;
      const Ref_Order = collection(db, "Order");
      const q3_1 = query(Ref_Order, where("customerId", "==", this.userQuery));
      const querySnapshot_1 = await getDocs(q3_1);
      querySnapshot_1.forEach((doc) => {
        if (this.temp.has(doc.data().productId)) {
          count++;
          this.temp.set(doc.data().productId, count);
        } else if (!this.temp.has(doc.data().productId)) {
          this.temp.set(doc.data().productId, 1);
        }
      });
      if (this.temp.size > 0) {
        const mapSort1 = new Map(
          [...this.temp.entries()].sort((a, b) => b[1] - a[1])
        );

        var key = Array.from(mapSort1.keys())[0];
        const Ref_Product = collection(db, "Product");
        const q3_2 = query(Ref_Product, where("pid", "==", key));
        const querySnapshot_2 = await getDocs(q3_2);
        querySnapshot_2.forEach((doc) => {
          this.result = doc.data().pName;
        });
        this.buycount = mapSort1.get(key);
      }
    },
  },
};
</script>
