<template>
  <div class="Q4">
    <h1>Q4</h1>
    <p style="margin-button: 10px">
      ex:(order_00000000)
    </p>
    <input v-model="userQuery" />
    <p>{{ userQuery }}  :</p>
    <p>{{ place }}</p>
    <button @click="qs4()" style="margin-top: 10px"></button>
  </div>
</template>

<script>
import { db } from "../db";
import { collection, query, where, getDocs } from "firebase/firestore";

export default {
  data() {
    return {
      userQuery: "",
      place: "",
      temp: "",
    };
  },
  methods: {
    async qs4() {
      this.place = "";
      this.temp = "";
      const Ref_Order = collection(db, "Order");
      const q4_1 = query(Ref_Order, where("orderId", "==", this.userQuery));
      const querySnapshot_1 = await getDocs(q4_1);
      querySnapshot_1.forEach((doc) => {
        this.temp = doc.data().customerId;
      });

      const Ref_Customer = collection(db, "customer");
      const q4_2 = query(Ref_Customer, where("customerId", "==", this.temp));
      const querySnapshot_2 = await getDocs(q4_2);
      querySnapshot_2.forEach((doc) => {
        this.place = doc.data().address;
      });
    },
  },
};
</script>
