<template>
  <div class="Q5">
    <h1>Q5</h1>
    <p style="margin-button: 10px">ex:(p0001)</p>
    <input v-model="userQuery" />
    <p>{{ userQuery }}  : {{ total }}</p>
    <button @click="qs5()" style="margin-top: 10px"></button>
  </div>
</template>

<script>
import { db } from "../db";
import { collection, query, where, getDocs } from "firebase/firestore";

export default {
  data() {
    return {
      userQuery: "",
      temp: "",
      total: 0,
      pname: "",
    };
  },
  methods: {
    async qs5() {
      const Ref_Product = collection(db, "Product");
      const q5_1 = query(Ref_Product, where("pid", "==", this.userQuery));
      const querySnapshot_1 = await getDocs(q5_1);
      querySnapshot_1.forEach((doc) => {
        this.total = doc.data().stock;
      });
    },
  },
};
</script>
