<template>
  <div class="Q1">
    <h1>Q1</h1>
    <p style="margin-button: 10px">ex:(p0001)</p>
    <input v-model="userQuery" />
    <ul v-for="item in customer" :key="item.id" style="list-style-type: none">
      <li style="border: 0.5rem solid; font-weight: bold">
        Id: {{ item.Id }}, Name: {{ item.Name }} <br />Address:
        {{ item.Address }} <br />Gender: {{ item.Gender }}, Phone:
        {{ item.Phone }}
      </li>
    </ul>
    <br />
    <button @click="qs1()" style="margin-top: 10px"></button>
  </div>
</template>

<script>
import { db } from "../db";
import { collection, query, where, getDocs } from "firebase/firestore";

export default {
  // name: "Home",
  // components: {
  // HelloWorld,
  // },

  data() {
    return {
      customer: [],
      temp: [],
      userQuery: "",
    };
  },
  created() {},
  methods: {
    async qs1() {
      //data
      this.customer = [];
      this.temp = [];

      //
      const Ref_Cart = collection(db, "Cart");
      const q1_1 = query(Ref_Cart, where("pid", "==", this.userQuery));
      const querySnapshot_1 = await getDocs(q1_1);
      querySnapshot_1.forEach((doc) => {
        if (this.temp.length < 10) {
          if (!this.temp.includes(doc.data().customerId)) {
            this.temp.push(doc.data().customerId);
          }
        }
      });
      const Ref_Customer = collection(db, "customer");
      const q1_2 = query(Ref_Customer, where("customerId", "in", this.temp));
      // console.log(this.temp);
      const querySnapshot_2 = await getDocs(q1_2);
      querySnapshot_2.forEach((doc) => {
        // doc.data() is never undefined for query doc snapshots
        // console.log(doc.id, " => ", doc.data());
        this.customer.push({
          Id: doc.id,
          Name: doc.data().name,
          Password: doc.data().password,
          Address: doc.data().address,
          Gender: doc.data().gender,
          Phone: doc.data().phone,
        });
      });
      // this.userQuery = "";
    },
  },
};
</script>
